<template>
  <div class="Lists">
    <div class="fixedspace"></div>
    <div ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <ul
        ref="loadmore"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
        infinite-scroll-immediate-check=false>
        <li v-for="(item,index) in list" @click="next(item,$event)">
          <!--<router-link :to="{name:'ListDetail'}">-->
          <div class="list-content">{{item.title}}</div>
          <!--</router-link>-->
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
  export default {
    name: 'Lists',
    data () {
      return {
        params: {
          page: this.$store.state.list.page == 1 ? 1 : this.$store.state.list.page,//页数
          limit: 16,//每一页的主题数量
        },
        wrapperHeight: 0,
      }
    },
    computed: {
      ...mapState({
        rollTop: 'roll'
      }),
      ...mapState('list', [
        'list',
      ])
    },
    watch: {},
    mounted(){
      this.SET_HEADERTEXT('列表页')
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      if (this.list.length) {
        document.body.scrollTop = this.rollTop;
      } else {
        this.getArticleList(this.params).then(res => {
          console.log(res);
        });
      }
    },
    methods: {
      ...mapMutations([
        'SET_HEADERTEXT',
        'SET_TOTOP'
      ]),
      ...mapActions('list', {
        getArticleList: 'list/GET_LIST',
        savePage: 'list/GET_PAGE'
      }),
      /*getArticleList(){
       var self = this;
       self.$http.get(self.$CONSTANTS.APIArticleList, {params: self.params}).then((res) => {
       //console.log(res);
       if (res.body.data.length) {
       //有长度触发无线滚动
       self.loading = false;
       res.body.data.forEach((item, index) => {
       self.list.push(item);
       });
       //console.log(self.list);
       } else {
       //数组长度为0表示已经全部加载完
       self.allLoaded = true;
       }
       }, (res) => {
       console.log(res)
       });
       },*/
      loadMore() {
        //this.showCoins = true;
        this.loading = true;
        this.params.page += 1
        this.savePage(this.params.page);
        this.getArticleList(this.params);
      },
      next(item, e){
        //e 获取当前点击元素的信息
        console.log(document.body.scrollTop)
        let roll = document.body.scrollTop;
        this.SET_TOTOP(roll);
        this.$router.push({name: 'ListDetail'})
      }
    }
  }
</script>

<style scoped>
  .list-content {
    width: 100%;
    padding: 0 .3rem;
    line-height: .8rem;
    background: #fff;
    border-bottom: 1px solid #f6f6f6;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
</style>



